.skill_tag {
    border: solid 1px transparent;
    border-radius: 5px;
    margin: 0.25rem;
    padding: 0.25rem 0.5rem;
    -webkit-box-shadow: 10px 10px 5px 0px rgba(235,235,235,1);
    -moz-box-shadow: 5x 5x 5px 0px rgba(235,235,235,1);
    box-shadow: 5px 5px 5px 0px rgba(235,235,235,1);
}

.job_hyperlink {
    text-decoration: underline;
    color: #495057;
}
.work_project {
    background-color: black;
    color: white;
}
.work_project a {
    color: white;
}

