
/* top banner - menu options */
.pf-options {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background-color: #000000;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    padding: 0 0.25rem;
    cursor: pointer;
  }

  .pf-opt {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1rem;
    text-decoration: none;
    color: white;
    padding: 0.25rem 0;
    font-weight: 500;
    font-weight: bold;
  }
  
  .pf-opt.active {
    border-bottom: 2px solid #cfb991;
    font-weight: bolder;
  }
  