/* Filter by Tags */

#tag_project {
  display: flex;
  flex-direction: row;
  padding: 1rem;
  justify-content: space-between;
  align-items: center;
}

#tag_project h1 {
  font-family: 'Aldrich', sans-serif;
  font-size: 3rem;
  margin: 0;
}

#search_div {
  width: 70%;
}
#tag_list {
  
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
#tag_list span {
  padding: 0.25rem 0.5rem;
  border-radius: 1px solid black;
}

/* Projects */
#project {
  margin: 0 1rem;
}
#pf_middle_pj {
    border: 1px solid lightgrey;
    border-radius: 10px;
    padding: 1rem;
    height: 100%;
}
.masonry {
    columns: 4;
    column-gap: 1rem;
}
  
  .masonry-item {
    break-inside: avoid-column;
    margin-bottom: 0.75rem;
    padding: 0.5rem;
    background-color: #f2f2f2;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    cursor: pointer;
  }
  .masonry-item h2{
    font-weight: bold;
    padding: 0.25rem 0;
  }
  .masonry-item h3{
    font-style: italic;
    color: #555960; 
  }
  .masonry-item h4{
    font-size: 0.9rem;
    padding: 0.75rem 0;
    color: darkslategray;
  }

  .filter-items {
    margin: 0.75rem 0;
    height: 100%;
    
    display: flex;
    flex-wrap: wrap;
  }
  
  .masonry-item h5{
    font-size: 1rem;

  }
  @media (max-width: 768px) {
    .masonry {
      columns: 1;
    }
  }