body {
  font-family: sans-serif;
  
  font-size: 16px;
}
.contents {
  margin-top: 50px; /* set margin top to the height of the top-bar */
  height: calc(100vh - 50px); /* set height to remaining viewport height */
  overflow-y: scroll;
  width: 100%;

}
.contents::-webkit-scrollbar,
body::-webkit-scrollbar {
  display: none;
}
#pf_right {
  margin-left: 240px;
  width: calc(100% - 240px);
  
}
.content {
  
  height: 100vh;
}