#work {
    padding: 3rem 1rem;
}
#work fieldset {
    border: 2px solid #8e6f3e;
    border-radius: 10px;
    
}
legend {
    font-size: 2rem;
    font-weight: bolder;
    text-align: center;
    background-color: white;
    padding: 0 1rem;
    font-family: 'Aldrich', sans-serif;
}

#work-content {
    width: 100%;
    margin: 0 auto;
    padding: 2rem 0;
    display: flex;
    justify-content: center;
    flex-direction: row;
  }

  .job {
    width: 30%;
    display: flex;
    justify-content: start;
    flex-direction: column;
    padding: 1rem;
    margin: 0 0.25rem;
    
  }
  
  .job h1 {
    font-weight: bold;
    font-size: 1.25rem;
    height: 30px;
    text-align: center;
    
    
    }
    .job h2 {
    font-size: 1.15rem;
    height: 30px;
    text-align: center;
    color: #555960;
    }
    .job h3 {
        font-size: 1.05rem;
        padding: 0.1rem 0;
        padding-bottom: 1rem;
        height: 25px;
        color: #555960;
        text-align: center;
        border-bottom: 1px solid #8e6f3e;

    }
    .job_desc {
        
        padding-top: 1rem;
    }
    .job_desc p {
        font-size: 1rem;
        padding: 0.25rem 0;
        
    }
    .job_skills {
        font-size: 1rem;
        padding-top: 0.75rem;
        
        display: flex;
        flex-wrap: wrap;
    }


   